.movie-view {
    display: grid;
    grid-template:
    [row1-start] "image title" auto [row1-end]
    [row2-start] "image description" auto [row2-end]
    [row3-start] "image director-genre" auto [row3-end]
    [row4-start] "image buttons" auto [row4-end]
    / 30% 70%;

    width: 100%;
    background-color: var(--background);
    color: var(--yellow);
    font-size: 1.4rem;
}

.movie-img {
    grid-area: image;
    width: 200px;
    height: 400px;
}

.movie-info {
    padding: var(--u3);
}

.movie-title {
    grid-area: title;
    margin: var(--u3) 0;
}

.movie-description {
    grid-area: description;
    margin-bottom: var(--u2);
}

.movie-view-buttons {
    display: flex;
    justify-content: space-between;
    margin-top: var(--u6);
}

.director-genre {
    grid-area: director-genre;
    display: flex;
    justify-content: space-between;

    span,  {
      font-size: 1.3rem;
      padding-right: 0 .5rem 0 0;
      border: none;
      margin: 0;
    }

    .director-genre-link {
        display: inline;
        background-color: var(--background);
        color: var(--yellow) !important;
        border: none;
        font-size: 1.6rem;
        padding: 0;
      
          &:hover,
          &:focus,
          &:active {
            background-color: var(--yellow);
            color: var(--background) !important;
            text-decoration: none;
          }
      }
}

@import "../../index.scss";
